import React, { memo } from 'react';
import { useStoreon } from 'storeon/react';
import BalancePageLayoutContainer from './BalancePageLayout/BalancePageLayoutContainer';

const BalancePage = ({
    context
}) => {
    const {currency, balanceStore, paymentsHistory} = useStoreon('currency', 'balanceStore', 'paymentsHistory' )
    const {
        cabinet_site_menu,
        historyPayment,
        cabinet_menu,
        breadcrumbs,
        isLoading,
        profile,
        shop,

    } = context;
    
    const { user = {}, role } = profile;
    const { balance, total_debt_orders, passive_balance, confirm_payments_cost, total_orders_price_unpaid, total_orders_price_paid } = balanceStore;

    const amountNotifications = profile?.notifications;
    const { username } = user;
    return (
        <BalancePageLayoutContainer
            total_orders_price_unpaid = { total_orders_price_unpaid }
            confirm_payments_cost={confirm_payments_cost}
            passive_balance={passive_balance}
            currency={currency}
            balance={balance}

            historyPayment={paymentsHistory}
            isLoading={paymentsHistory.isLoadingArchivePayments}
            
            amountNotifications={amountNotifications}
            cabinet_site_menu={cabinet_site_menu}
            cabinet_menu={cabinet_menu}
            breadcrumbs={breadcrumbs}
            create_shop={shop?.shop_link}
            is_has_shop={shop?.is_has_shop}
            username={username}
            role={role}
            shop={shop}
        />
    )
}

export default (BalancePage);